 /* ==========================================================================
    CLS (Cumulative Layout Shift) Prevention
    ========================================================================== */

 /* Show hero carousel content before OwlCarousel JS initializes */
 .js-main-slider:not(.owl-loaded) {
     display: block !important;
 }

 .js-main-slider:not(.owl-loaded) > .hero-slide {
     display: flex;
 }

 /* Show gallery carousel content before OwlCarousel JS initializes */
 .js-gallery-slider:not(.owl-loaded) {
     display: flex !important;
     overflow: hidden;
     gap: 15px;
 }

 .js-gallery-slider:not(.owl-loaded) > .home-gallery__slider-item {
     flex: 0 0 30%;
 }

 @media (max-width: 767px) {
     .js-gallery-slider:not(.owl-loaded) > .home-gallery__slider-item {
         flex: 0 0 45%;
     }
 }

 /* Reserve aspect ratio for about section images to prevent shift on load */
 .home-about .tab-pane img {
     aspect-ratio: 4 / 3;
     object-fit: cover;
 }

